* {
  position: relative;
}

html, body {
  height: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h3 {
  font-size: 16px;
  line-height: 1.5;
  margin: 0;
}

.px-32px {
  padding-left: 32px;
  padding-right: 32px;
}

.py-32px {
  padding-top: 32px;
  padding-bottom: 32px;
}

.mt-32px {
  margin-top: 32px;
}

.spacer {
  flex-grow: 1;
}

.logo {
  height: 40px;
  width: fit-content;
}

.menu {
  background-color: #f8f9fa;
  width: 296px;
  height: 100%;
  flex: 1 1 auto;
}

.function-name {
  display: flex;
  align-items: center;
}

.function-name .mdi {
  font-size: 24px;
  height: 24px;
  margin-right: 16px;
  display: flex;
  align-items: center;
}

.user-photo {
  background-image: url("../images/andychen.jpeg");
  background-size: cover;
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

.user-name {
  line-height: 19px;
}

.user-email {
  font-size: 14px;
  line-height: 17px;
  color: #6c757d;
}
